
a {
    text-decoration: none;
  }
  
  .middle{
    display: flex;
  }
  .btn{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 122px;
    height: 122px;
    background: #f1f1f1;
    margin: 10px;
    border-radius: 30%;
    box-shadow: 0 5px 15px -5px #00000070;
    color: #273c75;
    overflow: hidden;
    position: relative;
  }
  .btn i{
    line-height: 90px;
    font-size: 26px;
    transition: 0.2s linear;
  }
  .btn:hover i{
    transform: scale(1.3);
    color: #f1f1f1;
  }
  
  .btn div{
    /* line-height: 90px; */
    font-size: 26px;
    transition: 0.2s linear;
  }
  .btn:hover div{
    transform: scale(1.3);
    color: #f1f1f1;
  }
  
  .btn::before{
    content: "";
    position: absolute;
    width: 120%;
    height: 120%;
    background: #273c75;
    transform: rotate(45deg);
    left: -110%;
    top: 90%;
  }
  .btn:hover::before{
    animation: aaa 0.7s 1;
    top: -10%;
    left: -10%;
  }
  @keyframes aaa {
    0%{
      left: -110%;
      top: 90%;
    }50%{
      left: 10%;
      top: -30%;
    }100%{
      top: -10%;
      left: -10%;
    }
  }
  